import { useEffect, useState } from "react";
import axios from "axios";
import moment from "moment";

export default function PodcastList() {
  const [podcasts, setPodcasts] = useState([]);

  // DB   podcast  
  const getData = async () => {
    const res = await axios.get("/podcast/list");
    console.log(res.data);
    setPodcasts(res.data);
  };

  //     Controller  /podcast/list 
  useEffect(() => {
    getData();
  }, []);

  useEffect(() => {
    console.log(podcasts);
    console.log(moment(podcasts.start).format("YYYYMMDD hhmmss"));
    console.log(moment(podcasts.start).format("YYYYMMDD"));
    console.log(moment(podcasts.start).format("hhmmss"));
  }, [podcasts]);
  return (
    <>
      <div>!!!!</div>
      <div> </div>
      {podcasts &&
        podcasts.map((v, i) => {
          return (
            <ul key={i}>
              <li key={i}>podcast_no : {v.podcastNo}</li>
              <li>create_date : {v.createDate}</li>
              <li>modify_date : {v.modifyDate}</li>
              <li>hits : {v.hits}</li>
              <li>status : {v.status}</li>
              <li>total_streaming_time : {v.totalStreamingTime}</li>
            </ul>
          );
        })}
    </>
  );
}
